const CollectionInventory: React.FC<{ contract_address: string, name: string }> = ({ contract_address, name }) => {
  const [supply, setSupply] = useState<number | undefined>()
  const { ethereum, account } = useWallet();
  const [magePunkOwners, setMagePunkOwners] = useState<Record<string, string>>();
  const [myPunks, setMyPunks] = useState<number[]>([]);
  const signer = ethereum && (new ethers.providers.Web3Provider(ethereum)).getSigner();
  useEffect(() => {
    const nftContract: MagePunks | undefined = signer && MagePunks__factory.connect(contract_address, signer);
    nftContract && nftContract.totalSupply().then((amount) => {
      setSupply(amount.toNumber())
    });

  }, [account]);
  useEffect(() => {
    const nftContract: MagePunks | undefined = signer && MagePunks__factory.connect(contract_address, signer);
    supply && Array.from(Array(supply)).forEach((x, i) => {
      const id = i + 1;
      console.log(id);
      account && signer && nftContract?.ownerOf(i + 1).then((res) => {
        if (res === account) {
          setMyPunks(x => {
            if (x.includes(i + 1)) {
              return x;
            }
            return ([...x, i + 1])
          })
        }
        setMagePunkOwners(x => ({ [id]: res }));
      })
    });
  }, [account, supply])
  return (
    <>
      {myPunks.map((id) => <IonCol size='3'> <AsyncNFTCard address={contractAddress} id={id.toString()} name={name} /></IonCol>)}
    </>
  );
};



const ERC1155CollectionInventory: React.FC<{ contract_address: string, name: string }> = ({ contract_address, name }) => {
  const { ethereum, account } = useWallet();
  const [inventory, setInventory] = useState<Record<string, string>>();
  const signer = ethereum && (new ethers.providers.Web3Provider(ethereum)).getSigner();
  useEffect(() => {
    const nftContract: MagicalItem | undefined = signer && MagicalItem__factory.connect(contract_address, signer);
    Array.from(Array(5)).forEach((x, i) => {
      const id = i + 1;
      console.log(id);
      account && signer && nftContract?.balanceOf(account, i + 1).then((balance) => {
        console.log(balance);
        setInventory(x => ({
          ...x, [id]: balance.toNumber()
        }))
      })
    });
  }, [account])
  return (
    <>
      {inventory && Object.entries(inventory).map(([id, balance]) =>
        <>
          {balance && balance !== "0" ? <IonCol size="3"> <AsyncERC1155Card key={id} balance={balance} name='Magical Item' address={contract_address} id={id} /></IonCol> : <></>}
        </>)
      }
    </>
  );
};

