import { IonBadge, IonButton, IonButtons, IonChip, IonImg, IonText } from '@ionic/react';
import './ExploreContainer.css';
import Web3Modal from "web3modal";
import { ethers } from "ethers"
import { useWallet } from "use-wallet"
const LoginButton: React.FC = () => {

    const { connect, account, balance } = useWallet()
    const img = "https://smol.mypinata.cloud/ipfs/QmTWzBGYm8HrmiVRwcxarQ3yzUaXD3vHw5XcypZxYUq86S"
    if (account) {
        return <>
            <IonText color="success">
                {String(parseFloat(balance) / 1000000000000000000).slice(0, 4)} FTM
            </IonText>
            <IonChip color="success">
                <IonImg src={img} />
                {account.slice(0, 6)}...{account.slice(38, 55)}
            </IonChip>
            <IonButton color="medium" >
            </IonButton>
        </>
    }
    return (<IonButton fill='solid' color="primary" onClick={async () => {
        connect("injected")
    }}> Connect
    </IonButton >);
};

export default LoginButton;
