import { IonButtons, IonCard, IonCardContent, IonCardHeader, IonChip, IonCol, IonContent, IonGrid, IonHeader, IonImg, IonItem, IonItemDivider, IonLabel, IonPage, IonRow, IonText, IonTitle, IonToolbar } from '@ionic/react';
import { useEffect, useState } from 'react';
import ExploreContainer from '../components/ExploreContainer';
import LoginButton from '../components/LoginButton';
import './Tab1.css';
import { balloon } from 'ionicons/icons';
import { MagePunkCard, NFTCard } from './Tab1';
import { useParams } from 'react-router';
import { BigNumber, ethers } from 'ethers';
import { useWallet } from 'use-wallet';
import { MagePunks, MagePunks__factory } from '../contracts';
import { contractAddress } from './Tab3';

export interface NFTBalance {
  token_address: string;
  token_id: string;
  contract_type: string;
  owner_of: string;
  block_number: string;
  block_number_minted: string;
  token_uri?: string | undefined;
  metadata?: string | undefined;
  synced_at?: string | undefined;
  amount?: string | undefined;
  name: string;
  symbol: string;
}
interface NFTMetadata {
  token_address: string;
  name: string;
  abi?: string | undefined;
  supports_token_uri?: number | undefined;
  synced_at?: string | undefined;
  symbol: string;
  contract_type: string
}

const Tab2: React.FC = () => {

  const { id } = useParams<{ id: string }>()
  const { connect, ethereum, account } = useWallet()
  const signer = ethereum && (new ethers.providers.Web3Provider(ethereum)).getSigner();
  const [status, setStatus] = useState<"loading" | "idle" | "hidden">("loading");
  const [ownerAddress, setOwnerAddress] = useState("");
  const [magePunkAttributes, setMagePunkAttributes] = useState<undefined | any>()
  useEffect(() => {

    const nftContract: MagePunks | undefined = signer && MagePunks__factory.connect(contractAddress, signer);
    nftContract && nftContract.supply().then((amount) => {
      if (amount.toNumber() > parseInt(id)) {
        setStatus("idle");
      } else {
        setStatus("hidden");
      }
    });
    nftContract && id && nftContract.ownerOf(BigNumber.from(id)).then((address) => {
      console.log("RESULTS", address)
      setOwnerAddress(address);
    });
    (async () => {

      id && fetch("https://smol.mypinata.cloud/ipfs/QmVgzvxRiiGcB2jzwHYjYQU5JDeT6yFgLekPK2cwerenFw/" + (id) + ".json").then(async (res) => {
        const json = await res.json();
        setMagePunkAttributes(json);
      })

    })()

  }, [account, id])
  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <a href="/">
            <IonTitle>Magepunks</IonTitle>
          </a>
          <IonButtons slot="end">
            <LoginButton />
          </IonButtons>
        </IonToolbar>
      </IonHeader>
      <IonContent >
        <IonGrid>

          <IonRow>
            <IonCol />
            {magePunkAttributes && <MagePunkCard metadata={magePunkAttributes} />}
            {ownerAddress && <IonItem href={'https://ftmscan.com/address/' + ownerAddress}>
              <IonText color="medium">
                Owner {ownerAddress}
              </IonText>
            </IonItem>}
            <IonCol />
          </IonRow>
          
        </IonGrid>
      </IonContent>
    </IonPage >
  );
};

export default Tab2;
